import { Injectable } from '@angular/core';
import { Course } from './course';

@Injectable()
export class CourseService {
  courses = [
    {id: 1, name: 'Physics', sectionCnt: 1, classCnt: 1},
    {id: 2, name: 'Chemistry', sectionCnt: 1, classCnt: 2},
    {id: 3, name: 'Physics', sectionCnt: 2, classCnt: 3}
  ]

  constructor() { }

  getCourses(): Course[] {
    return this.courses;
  }
}



